header{
    background: #ffffff;
}
.header__line1{
    padding: 12px 0 11px;
    border-bottom: 1px solid rgba(85, 166, 48, 0.5);
}
.header__line1--block{
    display: flex;
    align-items: center;
}
.line1__block-logo{
    display: flex;
    align-items: center;
}
.line1__block-logo img{
    width: 61px;
    height: 61px;
}
.line1__block-logo span{
    color: #1C1E23;
    font-size: 11px;
    margin-left: 11px;
}
.line1__block-tel{
    display: flex;
    margin-left: auto;
    align-items: center;
    font-weight: 500;
    font-size: 25px;
    color: #424242;
}
.line1__block-tel::before{
    content: url(../../img/phone.svg);
    margin-right: 22px;
    top: 3px;
    position: relative;
}
.line1__block-soc{
    display: flex;
    align-items: center;
    margin: 0 66px 0 79px;
}
.line1__block-soc a:not(:last-child){
    margin-right: 25px;
}
.line1__block-current-lang{
    display: flex;
    align-items: center;
}
.arr{
    margin-left: 14px;
}
.header__line2--list{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 22px 0;
}
.header__line2--item{
    font-weight: 500;
    font-size: 16px;
    line-height: 26px;
    color: #424242;
}
