/* sec4 */
.sec4 {
  padding-top: 78px;
  padding-bottom: 100px;
}

.sec4__block {
  margin-top: 86px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.sec4__block-item {
  width: 364px;
  height: 260px;
  padding: 35px 28px 30px;
  margin-top: 55px;
  position: relative;
}

.sec4__block-item img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.special-block:hover .text1::after {
  content: url(../../img/longarrow.svg);
  position: absolute;
  left: 100%;
  bottom: 0;
}

.sec4__block-item:hover .twrap {
  justify-content: space-between;
}

.sec4__block-item:hover .twrap .text2 {
  display: block;
}

.sec4__block-item::before {
  background: rgba(35, 35, 35, 0.8);
  opacity: 0.6;
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
}

.special-block {
  width: 570px;
  margin-top: 0;
}

.sec4__block-item .twrap {
  position: relative;
  z-index: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.sec4__block-item .text1 {
  font-weight: 600;
  max-width: 327px;
  font-size: 32px;
  line-height: 38px;
  text-transform: uppercase;
  color: #FFFFFF;
  position: relative;
}

.sec4__block-item .text2 {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #FFFFFF;
  display: none;

}



